<template>
	<div class="create">
		<!-- <mt-header title="" class="createName">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div class="start">
			<img :src="memberList.wx_avatar" style="width: 70px;height: 70px;" v-if="memberList.wx_avatar">
			<img src="../../assets/photo.png" style="width: 70px;height: 70px;" v-if="!memberList.wx_avatar">
		</div>
		<div class="next start-btn">
			<router-link to="/register">
				<mt-button class="next-btn"></mt-button>
			</router-link>
		</div>
	</div>
</template>

<script>
	export default {
		data (){
			return {
				memberList: {}
			}
		},
		methods: {
			getMessage () {
				//
				var resource = this.$resource(this.$store.state.serverIP+'/users/get_user_info/?'+location.search.substring(1).split("&")[0]);
				resource.get().then((response) => {
					this.memberList = response.data;
				},(response) => {
					this.$toast({
        				message: '',
        				position: 'bottom'
        			});
				});
			}
		},
		mounted (){
			this.getMessage();
		}
	}
</script>

<style>
.start{
	width: 70px;
	height: 70px;
	margin: 0 auto;
	padding: 10px;
	margin-top: 100px;
	margin-bottom: 140px;
	border-radius: 50%;
	background-color: #f0f0f0;
}
</style>



